/*メイン動画のエフェクト用*/
@media print, screen and (min-width: 768px) {
  .--typeScrollMainVideo {
    --delay: 0;
    --duration: 800ms;
    --iterations: 1;
    opacity: 1;
  }
}

_::-webkit-full-page-media, _:future, :root .--typeScrollMainVideo {
  display: none;
  opacity: 0;
  animation: none;
}

_::-webkit-full-page-media, _:future, :root .--typeScrollMainVideo.active {
  display: none;
  opacity: 0;
  animation: none;
}

@media print, screen and (min-width: 768px) {
  .--typeScrollMainVideo.active,
.--typeScrollMainVideo.active::after {
    -webkit-animation-delay: var(--animation-delay, 2s);
    animation-delay: var(--animation-delay, 2s);
    -webkit-animation-iteration-count: var(--iterations, 1);
    animation-iteration-count: var(--iterations, 1);
    -webkit-animation-duration: var(--duration, 800ms);
    animation-duration: var(--duration, 800ms);
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
    animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  }
}

@media print, screen and (min-width: 768px) {
  _::-webkit-full-page-media, _:future, :root .--typeScrollMainVideo::after {
    content: none;
    opacity: 0;
    animation: none;
  }
}

@media print, screen and (min-width: 768px) {
  .--typeScrollMainVideo.active {
    opacity: 1;
    --animation-delay: var(--delay, 0);
    --animation-duration: var(--duration, 800ms);
    --animation-iterations: var(--iterations, 1);
    position: relative;
    -webkit-animation-name: clip-video;
    animation-name: clip-video;
    white-space: nowrap;
    cursor: default;
    animation-delay: 800ms;
  }
}
@media print, screen and (min-width: 768px) {
  .--typeScrollMainVideo.active::after {
    content: "";
    position: absolute;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f6f6f6;
    background-color: #FFF;
    transform: scaleX(0);
    transform-origin: 0 50%;
    pointer-events: none;
    -webkit-animation-name: video-revealer;
    animation-name: video-revealer;
    animation-delay: 800ms;
  }
}

@-webkit-keyframes clip-video {
  from {
    -webkit-clip-path: inset(0 0 0 0);
    clip-path: inset(0 0 0 0);
  }
  to {
    -webkit-clip-path: inset(100% 0 0 0);
    clip-path: inset(100% 0 0 0);
  }
}
@keyframes clip-video {
  from {
    -webkit-clip-path: inset(0 0 0 0);
    clip-path: inset(0 0 0 0);
  }
  to {
    -webkit-clip-path: inset(100% 0 0 0);
    clip-path: inset(100% 0 0 0);
  }
}
@-webkit-keyframes video-revealer {
  0%, 50% {
    transform-origin: 0 50%;
  }
  60%, 100% {
    transform-origin: 100% 50%;
  }
  60% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}
@keyframes video-revealer {
  0%, 50% {
    transform-origin: 0 50%;
  }
  60%, 100% {
    transform-origin: 100% 50%;
  }
  60% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}
